import { useState } from 'react';
import { Link } from 'react-router-dom';
import { UseProgressiveImage } from './UseProgressiveImage';
import placeholderBG from './images/placeholderBg.jpg'
import { HiAcademicCap, HiCommandLine, HiUserGroup, HiClock } from "react-icons/hi2";
import bg from './images/poly-bg.jpg'

const Testimonial = () => {
    const bgLoaded = UseProgressiveImage(bg);
    const [accordionItems, setAccordionItems] = useState([
        { id: 1, question: 'Question 1', answer: 'Answer 1' },
        { id: 2, question: 'Question 2', answer: 'Answer 2' },
        { id: 3, question: 'Question 3', answer: 'Answer 3' },
        // Add more FAQ items as needed
    ]);
    
    const toggleAccordion = (id) => {
        setAccordionItems((prevItems) =>
            prevItems.map((item) => ({
            ...item,
            isOpen: item.id === id ? !item.isOpen : false,
            }))
        );
    };

  return (
    <>
        <div name='testimonial' className='w-auto md:h-[calc(100vh-64px)] h-fit bg-gray-100 text-black'>
            <div className="relative w-full md:h-2/3 h-fit flex flex-col items-center bg-cover bg-top" 
            style={{ backgroundImage: `url(${bgLoaded || placeholderBG})`}}>
                <div className={`absolute inset-0 ${!bgLoaded && 'blur-sm'}`}></div>
                <div className='relative h-full flex flex-col justify-center items-center w-full max-w-6xl md:py-0 py-[5rem]'>
                    <h1 className='text-4xl font-bold tracking-wider pb-[3rem]'>Why InterEd?</h1>
                    <div className="w-full h-1/2 grid grid-cols-1 md:grid-cols-4 gap-6 text-center text-lg">
                        <div className='w-full h-full flex flex-col items-center'>
                            <HiAcademicCap size={48} className='mb-2'/>
                            <div className='p-3'>
                                <h1 className='text-2xl font-bold'>Comprehensive Curriculum.</h1>
                                <p className='mt-2'>Access a comprehensive curriculum that covers many aspects of computer science.</p>
                            </div>
                        </div>
                        <div className='w-full h-full flex flex-col items-center'>
                            <HiCommandLine size={48} className='mb-2'/>
                            <div className='p-3'>
                                <h1 className='text-2xl font-bold'>Interactive Learning.</h1>
                                <p className='mt-2'>Engage in interactive learning experiences to grasp complex concepts effortlessly.</p>
                            </div>
                        </div>
                        <div className='w-full h-full flex flex-col items-center'>
                            <HiUserGroup size={48} className='mb-2'/>
                            <div className='p-3'>
                                <h1 className='text-2xl font-bold'>Expert Instructors.</h1>
                                <p className='mt-2'>Learn from industry experts and experienced instructors.</p>
                            </div>
                        </div>
                        <div className='w-full h-full flex flex-col items-center'>
                            <HiClock size={48} className='mb-2'/>
                            <div className='p-3'>
                                <h1 className='text-2xl font-bold'>24/7 Access.</h1>
                                <p className='mt-2'>Access course materials and support whenever you need it.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='w-full h-fit md:h-1/3 flex items-center justify-center bg-gradient-to-r from-purple-500 via-indigo-600 to-sky-500 text-white'>
                <div className='max-w-6xl mx-4 py-[5rem]'>
                    <div className='h-full w-full flex flex-col items-center justify-center'>
                        <h1 className='text-4xl font-bold tracking-wider'>"InterEd has revolutionized my computer science learning approach."</h1>
                        <h1 className='text-3xl font-bold tracking-wider self-end mt-1'>-Example Student</h1>
                    </div>
                </div>
            </div>
        </div>
        <div className='w-auto md:h-[calc(100vh-64px)] h-fit bg-gray-100 text-black'>
            <div className='relative w-full md:h-2/3 h-fit flex flex-col items-center bg-gray-100 bg-cover bg-top'
            style={{ backgroundImage: `url(${bgLoaded || placeholderBG})`}}>
                <div className={`absolute inset-0 ${!bgLoaded && 'blur-sm'}`}></div>
                <div className='relative h-full flex flex-col justify-center items-center w-full max-w-6xl py-[5rem] px-4 z-10'>
                    <h1 className='text-4xl font-bold tracking-wider mb-[5vh]'>Got questions?</h1>
                    <div className='w-full'>
                    {accordionItems.map((item) => (
                            <div
                            key={item.id}
                            className='border border-black bg-gray-100/50 rounded-md mb-2 p-2'
                            onClick={() => toggleAccordion(item.id)}
                            >
                                <div className='flex justify-between cursor-pointer'>
                                    <h3 className='text-lg font-semibold'>{item.question}</h3>
                                    <span>{item.isOpen ? '▲' : '▼'}</span>
                                </div>
                            {item.isOpen && <p className='mt-2'>{item.answer}</p>}
                            </div>
                    ))}
                    </div>
                </div>
            </div>
            <div className='w-full h-fit md:h-1/3 flex items-center justify-center bg-gradient-to-b from-purple-500 via-indigo-600 to-sky-500 text-white'>
                <div className='max-w-6xl mx-2 py-[5rem]'>
                    <div className='h-full w-full flex flex-col items-center justify-center'>
                        <h1 className='text-4xl font-bold tracking-wider mb-[3vh]'>Ready? Let's learn.</h1>
                        <Link to='/login'>
                            <button className='bg-white hover:bg-black hover:text-white text-black duration-200
                             px-4 py-2 rounded-3xl font-bold tracking-wider text-xl shadow-xl hover:scale-105'>Get InterEd</button>
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    </>

  );
};



export { Testimonial as default}